import React, { ReactElement, useContext } from "react";
import LogoutIcon from "@mui/icons-material/Logout";
import {
  AppBar,
  Avatar,
  Box,
  Button,
  CircularProgress,
  Container,
  createTheme,
  CssBaseline,
  Divider,
  Grid,
  IconButton,
  Menu,
  MenuItem,
  Stack,
  ThemeProvider,
  Toolbar,
  Tooltip,
  Typography,
} from "@mui/material";
import { PropertyCard } from "./PropertyCard";
import { Property, usePropertiesQuery } from "../hooks/PropertyFetch";
import { BASE_API_URL } from "../util/Constants";
import { AuthContext } from "../AuthContext";
import { Link } from "react-router-dom";

const defaultTheme = createTheme();

function PropertiesGrid(props: {
  loading: boolean;
  error: any;
  propertiesArray?: Property[];
}) {
  const { loading, error, propertiesArray } = props;

  const placeholder =
    !loading && !error ? <Typography>No properties found!</Typography> : null;

  return (
    <Grid container spacing={4} justifyContent={"center"}>
      {loading && <CircularProgress />}
      {propertiesArray?.length
        ? propertiesArray.map((property, index) => (
            <PropertyCard key={index} property={property} />
          ))
        : placeholder}
    </Grid>
  );
}

export default function Dashboard(): ReactElement {
  const {
    response: propertyResponse,
    isLoading,
    error,
  } = usePropertiesQuery(`${BASE_API_URL}/properties`);
  const properties = propertyResponse?._embedded?.properties;

  const { logoutCallback, user, login } = useContext(AuthContext);

  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <AppBar position="relative">
        <Toolbar style={{ justifyContent: "space-between" }}>
          <Typography>Properties</Typography>
          {!user && (
            <Button
              color={"primary"}
              variant={"contained"}
              component={Link}
              to={`/login`}
            >
              Login
            </Button>
          )}
          {user && (
            <div
              style={{
                display: "inline-flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <Tooltip title="Account settings">
                <IconButton onClick={handleClick}>
                  <Avatar alt={user} src={login?.data?.avatarUrl}>
                    {(login?.data?.name ?? user)
                      .split(" ")
                      .map((str: string) => str.at(0)?.toUpperCase())
                      .join("")}
                  </Avatar>
                </IconButton>
              </Tooltip>
              <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{ sx: { margin: 1 } }}
              >
                {login?.data?.name && (
                  <Typography align={"center"} style={{ fontWeight: "bold" }}>
                    {login.data.name}
                  </Typography>
                )}
                <Typography align={"center"}>{user}</Typography>
                {login?.data?.rol?.includes("ADMIN") && (
                  <Typography color={"red"} align={"center"}>
                    (ADMIN)
                  </Typography>
                )}
                <Divider style={{ margin: 4 }} />
                <MenuItem
                  onClick={() => {
                    handleClose();
                    logoutCallback();
                  }}
                >
                  <LogoutIcon style={{ marginRight: 15 }} />
                  <Typography variant="inherit">Logout</Typography>
                </MenuItem>
              </Menu>
            </div>
          )}
        </Toolbar>
      </AppBar>
      <Box
        sx={{
          bgcolor: "background.paper",
          pt: 8,
          pb: 6,
        }}
      >
        <Container maxWidth="sm">
          <Typography
            variant="h5"
            align="center"
            color="text.secondary"
            paragraph
          >
            Available Properties
          </Typography>
          <Stack
            sx={{ pt: 4 }}
            direction="row"
            spacing={2}
            justifyContent="center"
          ></Stack>
        </Container>
      </Box>
      <PropertiesGrid
        loading={isLoading}
        error={error}
        propertiesArray={properties}
      />
    </ThemeProvider>
  );
}
